@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;700&display=swap');

*,
*::after,
*::before {
   box-sizing: border-box;
}

/*===================== 
   *Custom Properties
=====================*/
:root {
   --ff-primary: 'Manrope', sans-serif;

   --fw-reg: 400;
   --fw-bold: 500;
   --fw-bolder: 700;

   --clr-dark: #000000;
   --clr-lighter-dark: #101010;
   --clr-light: #ffffff;
   --clr-darker-light: #fafafa;
   --clr-grey: #f1f1f1;
   --clr-accent: #D87D4A;
   --clr-lighter-accent: #FBAF85;

   --fs-h1: 2.25rem;
   --fs-h2: 1.75rem;
   --fs-h3: 1.75rem;
   --fs-h4: 1.75rem;
   --fs-body: 0.9375rem;
   --fs-overline: 0.875rem;
   --fs-subtitle: 0.8125rem;
}

/* Media Queries Custom Properties */
@media (min-width: 376px) {
   :root {
      --fs-h1: 3.5rem;
      --fs-h2: 2.5rem;
      --fs-h3: 2rem;
   }
}

/*================= 
   *General Styles
=================*/
html,
body {
   width: 100%;
   height: 100%;
   margin: 0;
   position: absolute;
   top: 0;
   left: 0;
}

html {
   height: 100%;
}

body {
   margin: 0;
   min-height: 100%;
   overflow-x: hidden;
}

hr {
   margin: 0;
   border: none;
   border-bottom: 1px solid var(--clr-light);
   opacity: .1;
}

a {
   text-decoration: none;
}


section {
   margin-left: calc((24/375)*100%);
   margin-right: calc((24/375)*100%);
}

img {
   max-width: 100%;
}

ul {
   list-style: none;
   margin: 0;
   padding: 0;
}

button {
   border: 1px solid var(--clr-light);
   background: var(--clr-light);
   white-space: nowrap;
   cursor: pointer;
   outline: none;
   box-shadow: none;
}

a:active,
button:active {
   border: none;
   outline: none;
   box-shadow: none;
}

label,
input {
   display: block;
   text-decoration: none;
}

/* Media Queries General Styles */
@media (min-width: 376px) {
   section {
      margin-left: calc((40/768)*100%);
      margin-right: calc((40/768)*100%);
   }
}

@media (min-width: 769px) {
   :focus {
      outline: 2px solid #D87D4A;
      z-index: 1000;
   }

   section {
      margin-left: calc((165/1440)*100%);
      margin-right: calc((165/1440)*100%);
   }
}

/*============== 
   *Typography
==============*/
body {
   font-family: var(--ff-primary);
}

h1,
h2,
h3,
h4 {
   line-height: 1;
   margin: 0;
   text-transform: uppercase;
   letter-spacing: 1px;
}

h1 {
   font-size: var(--fs-h1);
}

h2 {
   font-size: var(--fs-h2);
}

h3 {
   font-size: var(--fs-h3);
}

h4 {
   font-size: var(--fs-h4);
}

p,
.paragraph {
   font-size: var(--fs-body);
   font-weight: var(--fw-bold);
   margin: 0;
   opacity: .5;
   line-height: 25px;
}

.header {
   color: var(--clr-light);
   background-color: var(--clr-dark);
}

.overline,
.overline-color-accent {
   margin: 0;
   letter-spacing: 10px;
   font-weight: var(--fw-reg);
   text-transform: uppercase;
}

.overline-color-accent {
   color: var(--clr-accent);
   opacity: 1;
}

a,
.link-btn {
   color: var(--clr-light);
   font-family: var(--ff-primary);
   font-weight: var(--fw-bolder);
   font-size: var(--fs-subtitle);
   text-transform: uppercase;
   letter-spacing: 2px;
}

.subtitle {
   font-size: var(--fs-subtitle);
}

.link-btn,
.product__add-to-cart-btn {
   align-self: center;
   padding: 15px 31px;
   letter-spacing: 1px;
}

.color-accent {
   color: var(--clr-accent);
}

.product__others-btn {
   background-color: var(--clr-accent);
}

label,
input {
   font-weight: var(--fw-bolder);
   font-family: var(--ff-primary);
}

label {
   letter-spacing: -.21px;
   font-size: .75rem;
}

input:focus {
   border: 1px solid var(--clr-accent);
   outline: none;
}

input {
   margin-top: 9px;
   font-size: var(--fs-overline);
   letter-spacing: -0.25px;
   color: var(--clr-dark);
   padding: 1.125em 1.5em;
   border: 1px solid #cfcfcf;
   border-radius: 8px;
   width: 100%;
   height: 56px;
   caret-color: var(--clr-accent);
}

input::placeholder {
   color: #000000;
   opacity: .4;
}

@media (min-width: 769px) {
   .link-btn:hover {
      background-color: var(--clr-lighter-accent);
   }
}

/* ==============
      *Modal
============== */
.modal {
   position: fixed;
   top: 93px;
   left: 0;
   background-color: var(--clr-light);
   z-index: 1000;
   overflow-y: auto;
   -ms-overflow-stvle: none;
   scrollbar-width: none;
}


.modal::-webkit-scrollbar {
   display: none;
}

.overlay {
   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   height: 100%;
   width: 100%;
   background: linear-gradient(rgba(255, 255, 255, 0) 90px,
         rgba(0, 0, 0, 0.4) 90px);
   z-index: 1000;
}

/* Nav modal  */
.nav-modal {
   width: 100%;
   height: calc((780/1000)*100%);
   border-bottom-right-radius: 8px;
   border-bottom-left-radius: 8px;
}


.nav_modal_container {
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   padding-bottom: 90px;
   -ms-overflow-stvle: none;
   scrollbar-width: none;
   overflow-y: scroll;
}

.nav_modal_container::-webkit-scrollbar {
   display: none;
}

/* Media Query navbar */
@media (min-width: 376px) {
   .nav-modal {
      height: 340px;

   }

   .nav_modal_container>.home__categories {
      margin-top: 120px;
   }
}

/*=========== 
   *Header
===========*/
.header {
   width: 100%;
}

/* nav */
.header__main {
   /* width: 98%; */
   height: 93px;
   display: flex;
   justify-content: space-between;
   align-items: center;
}

.header__link {
   padding: 2em 0;
}

.header__link+.header__link {
   margin-left: 2em;
}

.header__main--logo {
   padding: 2em 0;
}

/* cart */
.cart__logo-btn {
   margin: 0;
   cursor: pointer;
   background: unset;
   padding: 1.5em;
}

.cart__logo-btn:active {
   outline: none;
}

.cart__logo-container {
   width: 23.33px;
   position: relative;
}

.cart__logo-counter {
   margin: 0;
   position: absolute;
   top: 0;
   right: 0;
   transform: translate(50%, -50%);

   width: 17px;
   height: 17px;
   font-size: 13px;
   border-radius: 50%;
   color: var(--clr-light);
   background-color: var(--clr-accent);
   opacity: 1;

   display: flex;
   align-items: center;
   justify-content: center;
}

.cart__logo-counter-no-color {
   opacity: 0;
}

/* header bottom  */
.header__image {
   background-color: var(--clr-lighter-dark);
   background-image: url("../public/assets/home/mobile/image-header.jpg");
   background-size: cover;
   background-position: center -90px;
   background-blend-mode: difference;
   background-repeat: no-repeat;
   margin: 0;
}

.header__bottom {
   color: var(--clr-light);
   display: flex;
   flex-direction: column;
   align-self: center;
   gap: 1.75em;

   text-align: center;
   max-width: 328px;
   padding: 6.75em 0 7em;
   margin: 0 auto;
}

.header__bottom--new-product {
   opacity: .5;
}

.header__bottom--title {
   text-align: center;
   letter-spacing: 2px;
   line-height: 40px;
}

.header__bottom--btn {
   color: var(--clr-light);
   background-color: var(--clr-accent);
}

/* hamburger */
.header__main--hamburger,
.header__main--hamburger::after,
.header__main--hamburger::before {
   width: 16px;
   height: 3px;
   background-color: var(--clr-light);
   cursor: pointer;
}

.header__main--hamburger::after,
.header__main--hamburger::before {
   content: '';
   position: absolute;
}

.header__main--hamburger {
   display: block;
   position: relative;
}

.header__main--hamburger::after {
   bottom: 6px;
}

.header__main--hamburger::before {
   top: 6px;
}

/* Media Queries Header */
@media (min-width: 376px) {

   /* header main */
   .header__main {
      display: grid;
      grid-template-areas:
         "hamburger cart";
      align-items: center;
   }

   .header__main--hamburger {
      grid-area: hamburger;
   }

   .header__main>a {
      grid-area: hamburger;
      margin-left: 3.625em;
   }

   .header__main--cart-icon {
      grid-area: cart;
   }

   /* header bottom */
   .header__image {
      background-image: url("../public/assets/home/tablet/image-header.jpg");
   }

   .header__bottom {
      max-width: 379px;
      padding: 7.875em 0 10.4375em;
      gap: 2.5em;
   }

   .header__bottom--paragraph {
      max-width: 349px;
   }

   .header__bottom--title {
      font-size: var(--fs-h2);
   }

   .header__bottom--btn {
      margin-top: 1.5em;
   }
}

@media (min-width: 400px) {
   .header__bottom--title {
      font-size: var(--fs-h1);
      line-height: 58px;
   }
}

@media (min-width: 769px) {
   .header__link:hover {
      color: var(--clr-accent);
   }

   .header__main {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0 calc((165/1440)*100%);
   }

   .header__link+.header__link {
      margin-left: 2em;
   }

   .header__main--hamburger {
      display: none;
   }

   .header__main>a {
      margin-left: 0;
   }

   .header__main--logo {
      margin-left: 0;
   }

   hr {
      margin: 0 calc((165/1440)*100%);
   }

   /* header bottom */
   .header__image {
      background-image: url("../public/assets/home/desktop/image-hero.jpg");
   }

   .header__bottom {
      max-width: 398px;
      padding: 8em 0 9.875em;
      margin-left: calc((165/1440)*100%);

      align-items: flex-start;
      text-align: left;
      gap: 1.5em;
   }

   .header__bottom--title {
      text-align: left;
   }

   .header__bottom--btn {
      align-self: flex-start;
   }
}

/*========= 
   *Home
=========*/
/* Categories Nav */
.home__categories {
   margin-top: 5.75em;
   display: flex;
   flex-direction: column;
   gap: 4.25em;
}

.nav-modal-header {
   margin: 0;
}

.home__categories--nav {
   position: relative;
   background-color: var(--clr-grey);
   display: flex;
   flex-direction: column;
   align-content: center;
   text-align: center;
   gap: 1.0625em;
   padding: 5.5em 0 1.375em;
   border-radius: 8px;
   margin: 0 1.5em;
}

.home__categories--image {
   position: absolute;
   top: 0;
   left: 50%;
   transform: translate(-50%, -35%);

   max-width: 150px;
}

.home__categories--title {
   font-size: var(--fs-body);
   font-weight: var(--fw-bolder);
   letter-spacing: 1.07px;
   color: var(--clr-dark);
   opacity: 1;
}

.home__categories--shop-container {
   display: flex;
   align-items: center;
   justify-content: center;
   gap: 10px;
}

.home__categories--shop {
   color: var(--clr-dark);
   opacity: .5;
}

.home__categories--nav:hover .home__categories--shop {
   color: var(--clr-accent);
   opacity: 1;
}

.home__categories--right-arrow {
   width: 6.5px;
   height: 6.5px;
   border-top: solid 2px var(--clr-accent);
   border-right: solid 2px var(--clr-accent);
   transform: rotate(45deg);
}

/* Media Queries home Categories Nav */
@media (min-width: 376px) {
   .home__categories {
      margin-top: 9.25em;
      flex-direction: row;
      justify-content: space-between;
      gap: 0.625em;
   }

   .nav-modal-header {
      margin: 0;
   }

   .home__categories--nav {
      width: 223px;
      margin: 0;
   }
}

@media (min-width: 769px) {
   .home__categories {
      margin-top: 12.5em;
      gap: 1.875em;
   }

   .home__first-btn:focus {
      outline: 2px solid var(--clr-light);
      z-index: 10000;
   }

   .home__categories--nav {
      padding: 7.25em 0 1.875em;
      width: 350px;
   }

   .home__categories--image {
      max-width: unset;
      min-height: 150px;
      max-height: 180px;
   }
}

/* Products */
.home__products--sections {
   border-radius: 8px;
}

.home__products--sections+.home__products--sections {
   margin-top: 1.5em;
}

/* product 1 */
.home__products {
   margin-top: 7.5em;
}

.home__products--container-bottom>*,
.home__products--container-top {
   z-index: 2;
}

.home__products--item-top {
   background-color: #D87D4A;
   color: var(--clr-light);
   padding: 3.4375em 0;
}

.home__products--item-top,
.home__products--container-bottom {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   text-align: center;
   gap: 2em;
   overflow: hidden;
}

.home__products--container-top {
   position: relative;
}

.home__products--top-img {
   width: 172.25px;
}

.home__products--title-light {
   max-width: 200px;
   letter-spacing: 1.29px;
   line-height: 40px;
}

.home__products--paragraph {
   max-width: 280px;
   opacity: .75;
}

.home__products--btn-dark {
   background-color: var(--clr-dark);
   color: var(--clr-light);
}

.home__products--btn-dark:hover {
   background-color: #4c4c4c;
}

.home__products--btn:hover {
   background-color: var(--clr-dark);
   color: var(--clr-light);
}

/* Circles product 1 */
.home__products--circles,
.home__products--circles::after,
.home__products--circles::before {
   position: absolute;
   border: 1px solid rgba(255, 255, 255, .2);
   border-radius: 50%;
   z-index: -5;
}

.home__products--circles::before {
   content: '';
   width: 279px;
   height: 279px;
   left: 20.5px;
   top: 20.5px;
}

.home__products--circles {
   width: 320px;
   height: 320px;
   top: 0;
   left: 50%;
   transform: translate(-50%, -18%);
}

.home__products--circles::after {
   content: '';
   width: 558px;
   height: 558px;
   top: -119px;
   left: -119px;
}

/* Media Queries home product 1 */
