   .home__products--circles::after,
   .home__products--circles::before {
      transform: translateY(20px);
   }

   .home__products--circles::before {
      width: 502px;
      height: 502px;
      left: 35px;
      top: 15px;
   }

   .home__products--circles {
      width: 572px;
      height: 572px;
      top: 0;
      left: 50%;
      transform: translate(-45%, 70px);
   }

   .home__products--circles::after {
      width: 910px;
      height: 910px;
      top: -169px;
      left: -169px;
   }
}

