/*============= 
   *Category
=============*/

.category__header {
   display: flex;
   justify-content: center;
   align-content: center;
   background-color: var(--clr-dark);
   color: var(--clr-light);
   padding: 2em 0;
}

.category__product-container {
   margin-top: 4em;
}

.category__product-container+.category__product-container {
   margin-top: 7.5em;
}

.category__product-info {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   text-align: center;
   gap: 1.5em;
   margin-top: 2em;
}

.category__product-info-overline {
   color: var(--clr-accent);
   opacity: 1;
}

.category__product-info-title {
   line-height: 38.25px;
}

.category__product-info-title>span {
   display: block;
}

.category__product-info-btn {
   background-color: var(--clr-accent);
}

.category__categories {
   margin-top: 9.25em;
}

/* Media Queries Category Page  */
@media (min-width: 376px) {
   .category__header {
      padding: 6.25em 0;
   }

   .category__product-container {
      margin-top: 7.5em;
   }

   .category__product-info-title {
      line-height: 44px;
      letter-spacing: 1.43 px;
   }
}

@media (min-width: 769px) {
   .category__product-container {
      align-items: center;
      margin-top: 10em;
      display: flex;
      gap: 7.8125em;
   }

   .category__product-container:nth-last-child(odd) {
      flex-direction: row-reverse;
   }

   .category__product-info {
      align-items: flex-start;
      text-align: left;
      justify-content: flex-start;
      gap: 1em;
   }

   .category__product-info>p {
      margin-top: 1em;
   }

   .category__product-info-btn {
      align-self: flex-start;
      margin-top: 2.5em;
   }
}

/*=============== 
   *ProductPage
===============*/
.product__section-style {
   display: flex;
   flex-direction: column;
   align-items: flex-start;
   gap: 1.5em;
}

.go_back-btn {
   margin-top: 1em;
