/*=========== 
   *Cart
===========*/
/* Cart Modal */
.cart__modal {
   top: 117px;
   left: 50%;
   max-width: 327px;
   width: 100%;
   max-height: 70%;
   border-radius: 8px;
   transform: translateX(-50%);
}

.cart__modal_container {
   width: 100%;
   height: 100%;
   -ms-overflow-style: none;
   scrollbar-width: none;
   overflow-y: scroll;
}

.cart__modal_container::-webkit-scrollbar {
   display: none;
}

@media (min-width: 580px) {
   .cart__modal {
      max-width: 377px;

   }
}

@media (min-width: 710px) {
   .cart__modal {
      left: calc((351/768)*100%);
      right: calc((40/768)*100%);
      transform: unset;
   }
}

@media (min-width: 1040px) {
   .cart__modal {
      left: calc((898/1440)*100%);
      right: calc((165/1440)*100%);
   }
