/*============== 
   *Checkout
==============*/
.checkout {
   background-color: #f2f2f2;
   padding-bottom: 6em;
}

.checkout__container {
   margin-top: 1.5em;
   padding: 1.5em;
   background-color: var(--clr-light);
   border-radius: 8px;
}

.checkout__back {
   background-color: unset;
   opacity: 1;
   color: rgba(0, 0, 0, .5);
   letter-spacing: 0;
   text-transform: unset;
   padding: 1em 1em 1em 0;
   cursor: pointer;
}

.checkout__title {
   letter-spacing: 1px;
}

.checkout__form-container {
   margin-top: 2em;
}

.checkout__subtitle {
   margin: 0;
   margin-bottom: 1em;
   color: var(--clr-accent);
   font-size: var(--fs-subtitle);
   font-weight: var(--fw-bolder);
   letter-spacing: 0.93px;
   text-transform: uppercase;
}

.form-container+.form-container {
   margin-top: 1.5em;
}

/* invalid email */
.input__email-not-valid,
.input__email-not-valid:focus {
   border: 2px solid #CD2C2C;
}

.label__email-not-valid {
   position: relative;
   color: #CD2C2C;
}

.label__email-not-valid::after {
   content: 'Wrong format';
   position: absolute;
   top: 0;
   left: 100%;
   white-space: nowrap;
   transform: translateX(-100%);
   font-size: .75rem;
   font-weight: var(--fw-bold);
}

/* radio */
.checkout__radio * {
   cursor: pointer;
}

.checkout__radio {
   position: relative;

   padding: 1em;
   margin-top: 1em;

   display: flex;
   align-items: center;
   justify-content: flex-start;
   gap: 1em;

   width: 100%;
   height: 56px;

}

.radio_bottom {
   margin-bottom: 2em;
}

.checkout__radio>label,
.checkout__radio>input {
   max-height: 100%;
   padding: 20px;
}

.checkout__radio>label {
   margin: 0;
   width: 100%;
   padding-left: 0;
   align-self: center;
   display: flex;
   justify-content: flex-start;
   align-items: center;
}

.checkout__radio>input[type="radio"] {
   position: relative;
   -webkit-appearance: none;
   appearance: none;
   background-color: var(--clr-light);
   margin: 0;
   padding: 0;
   font-size: var(--fs-body);
   color: var(--clr-accent);
   max-width: 20px;
   height: 20px;
   border: 1px solid #cfcfcf;
   border-radius: 50%;
}

.checkout__radio>input[type="radio"]::before {
   content: '';
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%) scale(0);
   width: 10px;
   height: 10px;
   border-radius: 50%;
   background-color: var(--clr-accent);

   transition: 120ms transfrom ease-in-out;
}

.checkout__radio>input[type="radio"]:checked::before {
   transform: translate(-50%, -50%) scale(1);
}

.radio-border {
   position: absolute;
   top: 0;
   left: 0;
   height: 56px;
   width: 100%;
   border: 1px solid #c4c4c4;
   border-radius: 8px;
}

.radio-border:hover {
   border: 1px solid var(--clr-accent);
}

.checkout__radio>input[type="radio"]:focus+.radio-border,
.checkout__radio>input[type="radio"]:checked+.radio-border {
   border: 1px solid var(--clr-accent);
}

/* cash delivery */
.checkout__cash {
   margin-top: 1.5em;
   display: flex;
   flex-direction: column;
   align-items: flex-start;
   gap: 1em;
}

/* SUMMARY */
.checkout__summary {
   display: flex;
   flex-direction: column;
   justify-content: flex-start;
   align-items: flex-start;
   gap: 2em;
}

.summary__title {
   font-size: 1.125rem;
   font-weight: var(--fw-bolder);
}

.summary__products {
   width: 100%;
}

.summary__product {
   display: flex;
   align-items: center;
   width: 100%;
}

.summary__product+.summary__product {
   margin-top: 1.5em;
}

.summary__img {
   max-width: 64px;
   max-height: 64px;
   border-radius: 8px;
   margin-right: 1em;
}

.summary__product>div {
   display: flex;
   flex-direction: column;
   justify-content: flex-start;
   align-items: flex-start;
}

.summary__product>div>h5 {
   margin: 0;
   white-space: nowrap;
}

.summary__product-price {
   margin: 0;
   white-space: nowrap;
}

.summary__count-items {
   white-space: nowrap;
   margin-left: auto;
}

.summary__payment {
   width: 100%;
}

.summary__pay-container {
   display: flex;
   justify-content: space-between;
   align-items: center;
}

.summary__pay-container+.summary__pay-container {
   margin-top: 8px;
}

.summary__pay-container:last-child {
   margin-top: 1.5em;
}

.summary__label {
   letter-spacing: unset;
   text-transform: uppercase;
   white-space: nowrap;
}

.summary__price {
   font-size: 1.125rem;
   font-weight: var(--fw-bolder);
   opacity: 1;
}

.summary__pay-container:last-child>.summary__price {
   color: var(--clr-accent);
}

.summary__pay-btn {
   align-self: stretch;
}

