   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 3em;
}

.footer__contact--logo {
   position: relative;
}

.footer__contact--logo::before {
   content: '';
   width: 101px;
   height: 4px;
   background-color: var(--clr-accent);
   color: #D87D4A;
   position: absolute;
   top: 0;
   left: 50%;
   transform: translate(-50%, -52px);
   cursor: unset;
}

.footer__contact--nav {
   display: flex;
   flex-direction: column;
   /* gap: 1em; */
}

.footer__link {
   padding: 1.5em 1em;
}

.footer__link:hover,
.footer__contact--link:hover {
   color: var(--clr-accent);
}

.footer__contact--paragraph,
.footer__contact--copyright {
   color: var(--clr-light);
}

.footer__contact--copyright {
   font-weight: var(--fw-bolder);
}

.footer__contact--link {
   color: var(--clr-light);
   width: 24px;
   height: auto;
}

.footer__contact--links>ul {
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 1em;
}

/* Media Queries Footer */
@media (min-width: 470px) {
   .footer__contact {
      text-align: left;
      padding-top: 3.75em;
      padding-bottom: 2.875em;
   }

   .footer__contact--nav {
      flex-direction: row;
   }

   .footer__contact>section {
      display: grid;
      grid-template-areas:
         "logo _"
         "links links"
         "para para"
         "__ __"
         "copy social";
      row-gap: 2em;
   }

   .footer__contact--logo {
      grid-area: logo;
   }

   .footer__contact--nav {
      grid-area: links;
   }

   .footer__contact--paragraph {
      grid-area: para;
   }

   .footer__contact--copyright {
      grid-area: copy;
   }

   .footer__contact--links {
      grid-area: social;
      justify-self: end;
   }

   .footer__contact--logo::before {
      left: 0;
      transform: translate(0, -60px);
   }
}

@media (min-width: 769px) {
   .footer__contact {
      padding-top: 4.6875em;
      padding-bottom: 3em;
   }

   .footer__contact>section {
      grid-template-areas:
         "logo links links"
         "para para social"
         "para para social"
         "copy copy social";
      row-gap: 2.25em;
   }

   .footer__contact--logo {
      min-width: 143px;
   }

   .footer__contact--nav {
      justify-self: end;
   }

   .footer__contact--paragraph {
      max-width: 540px;
   }

   .footer__contact--logo::before {
      transform: translate(0, -89px);
   }
}